.container {
  width: 45%;
  margin: auto;
  border-radius: 45px;
  background: rgb(255, 255, 255);
  border: 2px solid rgba(0, 0, 0, 0.19);
}
.header {
  background: rgb(161, 125, 197);
  border-radius: 55px;
  color: white;
  margin: 0;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.header h1 {
  display: inline;
  margin-right: 42px;
}
.header img {
  height: 6vmin;
  float: left;
}
.data {
  font-size: 25px;
  margin: 10px 0;
  padding: 20px;
}
.priority {
  float: right;
}
.data img {
  float: right;
}
.text {
  text-align: none;
}

.style {
  text-decoration: line-through;
}
